import React, {useMemo} from "react";
import MarkdownJSX from "markdown-to-jsx";
import {Typography} from "@mui/material";

import {Code, CodeBlock} from "./blocks";


const headerRegExp = /---[\r\n]([\s\S]*)[\r\n]---/;

type Props = {
    content: string;
};

const Markdown: React.FC<Props> = (props) => {
    const {
        content = ""
    } = props;

    const markdown = useMemo(() => {
        return content.replace(headerRegExp, "");
    }, [content]);

    return (
        <MarkdownJSX
          options={{
            overrides: {
                code: {
                    component: Code
                },
                codeblock: {
                    component: CodeBlock
                },
                header: {
                    component: (props) => {
                        console.log(props);

                        return null;
                    }
                },
                h1: {
                    component: Typography,
                    props: {
                        sx: {md: 3},
                        variant: "h1"
                    }
                },
                h2: {
                    component: Typography,
                    props: {
                        sx: {md: 3},
                        variant: "h2"
                    }
                }
            }
          }}>
            {markdown}
        </MarkdownJSX>
    );
};


export {Markdown};
